<template>
  <div class="write">
    <b-container>
      <b-row class="justify-content-md-center">
        <div class="col-lg-8 col-sm-8">
          <b-form-input placeholder="" v-model="title"></b-form-input>
        </div>
        <div class="col-lg-2 col-sm-2">
          <b-button v-b-modal.modal-1 v-show="!editbtn"></b-button>
          <b-button v-b-modal.modal-2 v-show="editbtn"></b-button>
        </div>
      </b-row>

      <b-row class="mt-3 justify-content-md-center">
        <div class="col-lg-10">
          <quill-editor
            class="editor"
            ref="myTextEditor"
            v-model="content"
            :options="editorOption"
            @blur="onEditorBlur($event)"
            @focus="onEditorFocus($event)"
            @ready="onEditorReady($event)"
            @change="onEditorChange($event)"
          ></quill-editor>
        </div>
      </b-row>

      <b-modal id="modal-1" title="" @ok="release()">
        <span></span>
        <b-input placeholder="" v-model="label"></b-input>
        <span class="mt-3"></span>
        <b-input placeholder="" v-model="mold"></b-input>
      </b-modal>
    </b-container>
    <b-modal id="modal-2" title="" @ok="edit()">
        <span></span>
        <b-input placeholder="" v-model="label"></b-input>
        <span class="mt-3"></span>
        <b-input placeholder="" v-model="mold"></b-input>
      </b-modal>
   
    <!-- <div class="ql-container ql-snow">
      <div class="ql-editor"><code v-html="this.$store.state.contentext"></code></div>
    </div>-->
  </div>
</template>

 <script>
 
import moment from 'moment';
import 'moment/locale/zh-cn'
moment.locale('zh-cn');
import { mapState } from "vuex";
import { toRelease ,findId,editArticle} from "../../api/con";
export default {
  data() {
    return {
      image:"",
      author:"",
      title: "", //
      label: "", //
      mold: "", //
      currentime:"",//
      content: "", //
       editbtn:false,
      editorOption: {
        modules: {
          toolbar: [
            ["bold", "italic", "underline", "strike"], //    
            ["blockquote", "code-block"], //   
            [{ header: 1 }, { header: 2 }], // 12 
            [{ list: "ordered" }, { list: "bullet" }], // 
            [{ script: "sub" }, { script: "super" }], // /
            [{ indent: "-1" }, { indent: "+1" }], // 
            // [{'direction': 'rtl'}],                         // 
            [{ size: ["small", false, "large", "huge"] }], // 
            [{ header: [1, 2, 3, 4, 5, 6, false] }], // 
            [{ color: [] }, { background: [] }], // 
            [{ font: [] }], // 
            [{ align: [] }], // 
            ["clean"], // 
            ["link", "image", "video"] // 
          ] //
        },
        placeholder: "", //
        readyOnly: false, //
        theme: "snow", // snow/bubble
        syntax: true, //
        text: "",
      }
    };
  },
   mounted() {
     this.author = localStorage.getItem('username');
     this.$store.dispatch('getinfo',this.author);
    //  this.image = this.owninfo.image;
    //  console.log(this.image)
  },
   computed: {
    editor() {
      return this.$refs.myTextEditor.quillEditor;
    },
     ...mapState(["owninfo"])
  },
  methods: {
    // ...mapActions['submitData'],
    // 
    onEditorBlur(editor) {},
    // 
    onEditorFocus(editor) {},
    // 
    onEditorReady(editor) {},
    // 
    onEditorChange(editor) {
      this.content = editor.html;
      this.text = editor.text;
      // console.log(this.content);
      // console.log(this.text);
    },

    async release() {
      this.currentime = moment().format('YYYY-MM-DD HH:mm:ss');
      console.log(this.currentime);
      if(this.title!="" && this.content!="" && this.label !="" && this.mold != ""){
        const result = await toRelease( this.title, this.content,this.label,this.mold,this.currentime,this.author,this.owninfo.image);
        if(result.data.success_code === 200){
            this.$message({
            message: "",
            type: "success"
          });
          this.title = "";
          this.content = "";
          this.$router.push('/owner')
       }else{
            this.$message.error('');
       } 
      }else{
        this.$message({
            message: "",
            type: "warning"
          });
      }

     
    },
    
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.editor {
  line-height: normal !important;
  height: 500px;
}
.ql-snow .ql-tooltip[data-mode="link"]::before {
  content: ":";
}
.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  border-right: 0px;
  content: "";
  padding-right: 0px;
}

.ql-snow .ql-tooltip[data-mode="video"]::before {
  content: ":";
}

.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before {
  content: "14px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
  content: "10px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
  content: "18px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
  content: "32px";
}

.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
  content: "1";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
  content: "2";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
  content: "3";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
  content: "4";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
  content: "5";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
  content: "6";
}

.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
  content: "";
}
.write {
  margin-top: 15px;
}
</style>



